import React from 'react';
const iconImg = require('../images/sole.jpg');

function Article(props) {
    return (
        <div className="row">
            <div className="col-12 col-lg-9 col-xl-7 mb-3 articles-xl">
                <div className="row d-flex justify-content-around mx-auto">
                    <div className="col-6 col-sm-4 col-lg-4 embed-responsive embed-responsive-21by9">
                        <img src={require(`../images/${props.src}`)} alt="Shoes" className="images embed-responsive-item"></img>
                    </div>
                    <div className="col-5 col-sm-7 col-lg-7 articles">
                        <div className="row">
                            <div className="col-12 article-author d-inline-flex">
                                <img src={iconImg} alt="images" className="images "></img>
                                <h6 className="py-1 ml-1">SOLE COLLECTOR</h6>
                            </div>
                        </div>
                        <p>{props.title}</p>
                    </div>    
                </div>
            </div>
        </div>
    )
}

export default Article;