import React, { Component } from 'react';
import More from './More.js';
import Menu from './Menu.js';

const logo = require('../images/logo.png');
const logoIcon = require('../images/logo-icon.jpg');
const shopIcon = require('../images/shop-icon.jpg');

class Nav extends Component {

    mSearch() {
        const searchBtn = document.getElementById('search-btn-m');
        searchBtn.classList.toggle('fa-search');
        searchBtn.classList.toggle('fa-times');

        const searchBar = document.getElementById('searchBar-m');
        searchBar.classList.toggle('hide');
        
    }

    altSearch() {
        const searchBtn = document.getElementById('search-btn-alt');
        searchBtn.classList.toggle('fa-search');
        searchBtn.classList.toggle('fa-times');

        const searchBar = document.getElementById('searchBar');
        searchBar.classList.toggle('hide');
        
    }

    wSearch() {
        const searchBtn = document.getElementById('search-btn-w');
        searchBtn.classList.toggle('fa-search');
        searchBtn.classList.toggle('fa-times');

        const searchBar = document.getElementById('searchBar');
        searchBar.classList.toggle('hide');
        
    }

    searchAlert() {
        alert("Search bar functionality not included in this clone project")
    }

    render() {
    return (
    <div>
        <nav className="row fixed-top mobile">
            <div className="col-12 col-sm-11 mx-auto">
                <div className="row my-2 text-center">
                    <div className="col-3 text-left">
                        <Menu />
                    </div>                
                    <div className="col-6 text-center">
                        <img src={logo} className="logo"></img>
                    </div>            
                    <div className="col-3 text-right">
                    <i id="search-btn-m" className="fa fa-search fa-lg" onClick={this.mSearch.bind(this)}></i>
                        <img src={shopIcon} className="shop-logo"></img>
                    </div>
                </div>
            </div>
            <div className="row hide search" id="searchBar-m">
                <div className="col-12">
                    <div className="row d-flex justify-content-center">
                        <div className="col-8 text-center">
                            <input placeholder="Search"></input>
                        </div>
                        <div className="col-1 mt-3">
                            <i title="Search" className="fa fa-search fa-lg" onClick={this.searchAlert.bind(this)}></i>
                        </div>
                    </div>
                </div>
            </div>
        </nav>

        <nav className="row py-3 fixed-top ml-3 web">
            <div className="col-lg-2 col-xl-1 d-inline-flex justify-content-around">
                <img src={logo} alt="Logo" title="Logo" className="logo"></img> 
            </div>
            <div className="col-lg col-xl d-inline-flex justify-content-lg-between justify-content-xl-around ">
                <h6 className="">MUSIC</h6>
                <h6 className="">STYLE</h6>
                <h6 className="">POP CULTURE</h6>
                <h6 className="">SPORTS</h6>
                <h6 className="">LIFE</h6>
                <h6 className=" active">SNEAKERS</h6>
                <div className="d-inline-flex icon">
                    <img src={logoIcon} alt="Logo"></img>
                    <h6 className="pl-1 pr-2">SHOP</h6> 
                </div>
                <h6 className="">SHOWS</h6>
                <h6 className=""><More /></h6>
            </div>
            <div className="col-lg-2 col-xl-2 d-inline-flex justify-content-lg-center alt ">
                <i id="search-btn-alt" className="fa fa-search fa-lg" onClick={this.altSearch.bind(this)}></i>
            </div>

            <div className="col-xl-3 d-inline-flex social-icons mr-2">
                <i id="search-btn-w" className="fa fa-search fa-lg px-2" onClick={this.wSearch.bind(this)}></i>
                <i className="fa fa-lg fa-facebook-f px-2" title=""></i>
                <i className="fa fa-lg fa-twitter px-2" title=""></i>
                <i className="fa fa-lg fa-instagram px-2" title=""></i>
                <i className="fa fa-lg fa-youtube px-2" title=""></i>
                <i className="fa fa-lg fa-snapchat-ghost px-2" title=""></i>
                <i className="fa fa-lg fa-headphones px-2" title=""></i>
            </div>
            <div className="row hide search" id="searchBar">
                <div className="col-12">
                    <div className="row d-flex justify-content-center">
                        <div className="col-8 text-center">
                            <input placeholder="Search"></input>
                        </div>
                        <div className="col-1 mt-3">
                            <i title="Search" className="fa fa-search fa-lg" onClick={this.searchAlert.bind(this)}></i>
                        </div>
                    </div>
                </div>
            </div>
        </nav>

    </div>

    )
}
}
export default Nav;