import React from 'react';
const banner = require('../images/banner.jpg');
const ad = require('../images/ad.jpg');

function Banner() {
    return (
        <div>
            <div className="row advertisement">
                <div className="col col-sm-9 col-md-6 col-xl-3 mx-auto">
                    <img src={ad} alt="advertisement" className="images"></img>            
                </div>
            </div>
            <div className="row">
                <div className="col-11 col-md-11 col-lg-8 col-xl-7 mx-auto mobile-title">
                    <h1 className="py-3">A Complete Guide to Alex's Sneakers</h1>
                </div>
            </div>
            <div className="container">
                <div className="row pt-2 pb-3">
                    <div className="col-12 col-md-10 col-lg-8 d-inline-flex justify-content-between mx-auto">
                        <p className="text-left author">BY <a href="https://al-byte.github.io/Portfolio-Alt/">ALEX WASHINGTON</a></p>
                        <p className="text-right date">May 4, 2020</p>
                    </div>
                </div>
            </div>
                
            <div className="row">
                <figure className="col col-lg-10 col-xl-6 mx-auto">
                    <img src={banner} alt="banner" id="banner" className="mb-2"></img>
                    <figcaption className="text-center"><i>All Images Courtesy of Google Images</i></figcaption>
                </figure>
            </div>
            <div className="row">
                <div className="col-11 col-md-10 col-lg-8 col-xl-5 py-5 introduction mx-auto border-bottom">
                
                    <p>Alex Washington: sneakerhead. Yup. Not in the traditional 
                        sense, but over the course of his life, Alex wore his fair 
                        share of Nike bangers and obscure white on white trainers. 
                        We thought documentation of this 1990s baby was warranted, 
                        so Complex tapped its go-to footwear historian Alex Washington 
                        to painstakingly sort through Alex's greatest hits. We've provied 
                        images when we had them, here they are in their entirety.
                    </p>
                    <p className="text-center bold"><strong>***DISCLAIMER!!!***</strong></p>
                    <em>This page is strictly a replica project of a Complex page article 
                        designed and developed with React. Most links are not active. I am 
                        in no way, shape, or form affiliated with Complex.com, Complex 
                        Magazine, Complex Networks, or any other incarnation of the Complex brand.
                    </em>
                    
                </div>
                
            </div>
        </div>
    )
}

export default Banner;